import axios from 'axios';
import {PostsAPILink} from './data';
import PostProcessor from './components/PostProcessor';

require('../scss/pages/news.scss');

window.addEventListener('load', ()=>{
  const News = document.getElementById('news-wrapper');
  axios.get(`${PostsAPILink}posts`).then((res)=>{
    PostProcessor(res.data, News, true);
  })
  .catch((err)=>{
    console.log(`Error retrieving posts - ${err}`)
  });
});